<template>
	<div class="login">
		<el-form label-width="100px" class="demo-ruleForm login-container el-form--label-left">
			<h3 class="title">系统登录</h3>
			<div class="el-form-item">
				<el-input type="text" auto-complete="off" placeholder="账号" v-model="username"></el-input>
			</div>
			<div class="el-form-item">
				<el-input type="password" auto-complete="off" placeholder="密码" v-model="password"></el-input>
			</div>
			<el-checkbox class="remember" v-model="checked">记住密码</el-checkbox>
			<div class="el-form-item">
				<el-button type="primary" :loading="loading" @click="submit" style="width: 100%;">登录</el-button>
			</div>
		</el-form>
	</div>
</template>

<script>
	import {
		mapMutations
	} from 'vuex'
	import {
		doUserLogin
	} from '../api/api'
	var _this
	export default {
		data() {
			return {
				username: 'admin',
				password: 'admin',
				checked: true,
				loading: false
			}
		},
		created() {
			_this = this
		},
		methods: {
			...mapMutations([
				'saveUserInfo'
			]),
			submit: function() {
				_this.loading = true
				if (_this.username == 'admin' && _this.password == 'admin') {
					let userInfo = {
						username: _this.username
					}
					_this.$store.commit('saveUserInfo', JSON.stringify(userInfo))
					_this.$router.push('/')
				} else {
					_this.$message.error('账号或密码不正确');
				}
				setTimeout(function() {
					_this.loading = false
				}, 2000)
				return;

				_this.$http.post(_this.base + "/user/login", {
					username: _this.username,
					password: _this.password
				}).then(function(res) {
					console.log(res)
					_this.loading = false
					if (res.data.code == 200) {
						let userInfo = {
							username: _this.username
						}
						_this.$store.commit('saveUserInfo', JSON.stringify(userInfo))
						_this.$router.push('/')
					} else {
						_this.$message.error(res.data.message)
						return false
					}
				})
			}
		}
	}
</script>

<style>
	.login {
		width: 100%;
		height: 100%;
		background: url(../assets/image/login-bg.jpg) no-repeat center center;
		background-size: 100% 100%;
		position: absolute;
		left: 0;
		top: 0;
	}

	.login-container {
		border-radius: 5px;
		-moz-border-radius: 5px;
		background-clip: padding-box;
		margin: 180px auto;
		width: 350px;
		padding: 35px 35px 15px;
		background: #fff;
		border: 1px solid #eaeaea;
		box-shadow: 0 0 25px #cac6c6;
	}

	.el-form--label-left {
		text-align: left;
	}

	.login-container .title {
		margin: 0 auto 40px;
		text-align: center;
		color: #505458;
	}

	.el-form-item {
		margin-bottom: 20px;
	}

	.login-container .remember {
		margin: 0 0 30px;
	}
</style>
